.home {
  .logo {
    background: white;
    height: 100px;
    width: 200px;
  }

  .info-section {
    padding: 54px;
    width: 35%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;

    .info-box {
      position: absolute;
      bottom: 60px;

      h4 {
        font-size: 1.2rem;
        color: $color3;
        margin: 0.7rem 0;
      }

      p {
        font-size: 0.9rem;
        font-weight: 300;
        color: $color3;
        margin: 0.2rem 0 1.6rem;
      }
    }
  }

  .content-area {
    margin-left: 35%;
    width: 65%;
    background: white;
    height: 100%;

    .img-container {
      display: inline-block;
      width: 30%;
      position: relative;
      height: 100%;

      .user-img {
        position: absolute;
        top: 56px;
        left: -250px;
        width: 500px;
        height: calc(100% - 112px);
        background: url("/img/model_img.jpg") no-repeat center center;
        -webkit-background-size: cover;
        -moz-background-size: cover;
        -o-background-size: cover;
        background-size: cover;
      }
    }

    .profile-section {
      display: inline-block;
      vertical-align: top;

      .profile {
      }
    }
  }
}
